export const startSession = () => {
    return {
        "type": "start_session",
        "session_type": "leetcode_interview",
        "params": {
            "leetcode_problem_number": 123
        }
    };
}
export const sendMessage = (message: string, type: string) => {
    return {
        "type": "send_message",
        "message": message,
        "message_type": type
    };
}

export type ReceivedMessage = {
    type: string,
    message: string,
    message_type: 'text' | 'error',
    parameters: {
        stream : boolean,
        is_end : boolean,
    }
};