import React, {useEffect, useRef, useState} from "react";
import {Alert, Button, Container, Form, Nav, Spinner} from "react-bootstrap";
import {BoxArrowUpRight, PlayCircle, SendFill, StopCircle, Trash3Fill} from "react-bootstrap-icons";

import "./ChatBox.css";

import { useDispatch, useSelector } from "../store";
import { sendChat, removeMessage } from "../slices/message";
import { enableSpeech, disableSpeech } from "../slices/speech";
import { format } from "./format";
import Message from "./Message";


const ChatBox = () => {
  const [chatText, setChatText] = useState('');
  const [pinElem, setPinElem] = useState<{ text: string, elem: HTMLDivElement | null } | null>(null);
  const messageWrapper = useRef<HTMLDivElement>(null);

  const dispatch = useDispatch();
  const colorScheme = useSelector(state => state.setting.colorScheme);
  const messageState = useSelector(state => state.message);
  const speechState = useSelector(state => state.speech);

  const scrollToButtom = () => {
    if (messageWrapper.current) {
      const scrollHeight: number = messageWrapper?.current?.scrollHeight || 0;
      messageWrapper.current?.scrollTo(0, scrollHeight + 10);
    }
  }

  useEffect(scrollToButtom, [messageState.messages])

  const handleChatSubmit = () => {
    dispatch(sendChat(chatText));
    setChatText("");
  }
  const handlePinGoto = () => {
    if (pinElem && pinElem.elem) {
      pinElem.elem.scrollIntoView();
    }
  }
  const handlePinDelete = () => {
    setPinElem(null);
  }

  const onChatChange = (event: any) => {
    setChatText(event.target.value);
  }
  const onPin = (text: string, elem: HTMLDivElement | null) => {
    setPinElem({text, elem});
  }
  const onMessageRemove = (index: number) => {
    dispatch(removeMessage(index));
  }

  const onToggleSpeech = () => {
    if(speechState.isEnabled){
      dispatch(disableSpeech());
    }else{
      dispatch(enableSpeech());
    }
  }

  return (<>
    <Nav id="speech-control" className='justify-content-end align-items-end'>
      <Nav.Item className='align-self-center control-text'>
        Speech : {speechState.isEnabled ? "enabled" : "disabled"}
      </Nav.Item>
      <Nav.Item>
        <Button size='lg' variant='link' className="control-button" onClick={onToggleSpeech}>
          {speechState.isEnabled ? <StopCircle/> : <PlayCircle/>}
        </Button>
      </Nav.Item>
    </Nav>
    {pinElem ?
      <Alert id="pinned-text" variant='warning' className='message'
             style={{
               backgroundColor: (colorScheme === 'dark') ? "transparent" : "var(--bs-alert-bg)",
               color: (colorScheme === 'dark') ? "var(--bs-alert-border-color)" : "var(--bs-alert-color)"
             }}
      >
        <div id="pinned-wrap">
          {format(pinElem.text, colorScheme)}
        </div>
        <div className="message-buttons">
          <Button variant='link' onClick={handlePinGoto}><BoxArrowUpRight/></Button>
          <Button variant='link' onClick={handlePinDelete}><Trash3Fill/></Button>
        </div>
      </Alert>
      : ""}
    <Container ref={messageWrapper} className='wrapper'>
      <div tabIndex={-1} id='messages'>
        {messageState.messages.map((msg, idx) =>
          <Message key={idx} index={idx} content={msg} onPin={onPin}
                   onRemove={onMessageRemove.bind(this, idx)}/>
        )}
      </div>
    </Container>
    <hr style={{marginTop: 0}}/>
    <Container className="footer">
      <Form.Control as="textarea" className='shadow-none'
                    placeholder="message..."
                    onChange={onChatChange}
                    rows={1}
                    style={{
                      resize: 'none',
                      height: `${chatText.split("\n").length + 1}em`,
                      color: 'inherit',
                      backgroundColor: 'transparent'
                    }}
                    value={chatText}
                    onKeyDown={(e) => {
                      if ((e.metaKey || e.ctrlKey) && e.key === 'Enter') {
                        handleChatSubmit();
                      }
                    }}
      />
      <Button id="chat-send-button"
              variant="primary"
              onClick={handleChatSubmit}
              disabled={messageState.isSending}
      >
        {messageState.isSending ? <Spinner style={{height: '1em', width: '1em'}} animation="border" variant='light'/> :
          <SendFill/>}
      </Button>
    </Container>
  </>)
}

export default ChatBox;
