import React, { useRef } from "react";
import { Alert, Button } from "react-bootstrap";
import {format} from './format';
import './Message.css';
import { PinAngle, Trash3Fill } from "react-bootstrap-icons";

import { MessageType } from "../slices/message";
import { useSelector } from "../store";

function Message(props: {
    index: number,
    content: MessageType,
    onPin?: (text: string, elem: HTMLDivElement | null) => void,
    onRemove?: (index: number) => void,
}){
    const isUser = (props.content.role === 'user');
    const elemRef = useRef<HTMLDivElement>(null);
    let variant = isUser ? 'success' : 'primary';
    if(props.content.type === "error"){
        variant = "danger";
    }
    const colorScheme = useSelector(state => state.setting.colorScheme);

    const handlePinClicked = () => {
        if(props.onPin) props.onPin(props.content.text, elemRef.current);
    }
    const handleRemove = () => {
        if(props.onRemove) props.onRemove(props.index);
    }

    return (
    <Alert className="message"
        ref={elemRef}
        style={{
            float: isUser ? 'right' : 'left',
            backgroundColor: (colorScheme === 'dark') ? 'transparent' : 'var(--bs-alert-bg)',
            color: (colorScheme === 'dark') ? 'var(--bs-alert-border-color)' : 'var(--bs-alert-color)',
        }}
        variant={variant}
    >
        <div className="message-text">{format(props.content.text, colorScheme)}</div>
        <div className="message-buttons">
            {Boolean(props.onPin && (props.content.type !== "error")) && <Button variant='link' onClick={handlePinClicked}><PinAngle/></Button>}
            <Button variant='link' onClick={handleRemove}><Trash3Fill/></Button>
        </div>
    </Alert>
    )
}

export default Message;
