import React, { useState } from "react";
import { Button, Container, Spinner } from "react-bootstrap";
import CodeMirror from '@uiw/react-codemirror';
import { languages } from './format';
import { ArrowBarUp } from "react-bootstrap-icons";
import { useDispatch, useSelector } from "../store";
import { sendCode } from "../slices/message";

import "./EditorBox.css";

const EditorBox = () => {
  const [codeText, setCodeText] = useState('');
  
  const dispatch = useDispatch();
  const colorScheme = useSelector(state => state.setting.colorScheme);
  const isSending = useSelector(state => state.message.isSending);

  const handleCodeSubmit = () => {
    dispatch(sendCode(codeText));
  }

  return (<>
    <Container className='wrapper'>
      <CodeMirror
        id='editor'
        theme={(colorScheme === 'dark') ? 'dark' : 'light'}
        height='100%'
        value={codeText}
        extensions={languages}
        onChange={setCodeText}
      />
    </Container>
    <hr style={{marginTop: 0}}/>
    <Container className="footer">
      <Button
        variant="success"
        onClick={handleCodeSubmit}
        disabled={isSending}
      >
        {isSending ? "" : "Send code "}
        {isSending ? <Spinner style={{height: '1em', width: '1em'}} animation="border" variant='light'/> :
          <ArrowBarUp/>}
      </Button>
    </Container>
</>)
}

export default EditorBox;