import {createAsyncThunk, createSlice} from '@reduxjs/toolkit';
import authApi from "../apis/authApi";

export type UserState = {
  token: string | null,
  currentUser: string | null,
};

export type LoginRequest = {
  username: string,
  password: string,
}

type LoginResponse = {
  access_token: string,
  token_type: ''
}


const initialState: UserState = {
  token: null,
  currentUser: null,
};


export const userSlice = createSlice({
  name: 'user',
  initialState,
  reducers: {
    onLogout: (state: UserState) => {
      state.token = null;
    },
  },
  extraReducers: (builder) => {
    builder.addCase(login.fulfilled, (state: UserState, action) => {
      console.log('login success', action);
      state.token = action.payload.access_token;
      state.currentUser = action.meta.arg.username
    })
  },
});


export const login = createAsyncThunk<LoginResponse, LoginRequest>(
  'users/login',
  async ({username, password}: LoginRequest) => await authApi.login(username, password)
);

export const logout = () => async (dispatch: any) => {
  dispatch(userSlice.actions.onLogout());
}

export default userSlice.reducer;
