import { configureStore } from "@reduxjs/toolkit";
import reduxWebsocket from "@giantmachines/redux-websocket";
import {
    useDispatch as ReduxUseDispatch,
    useSelector as ReduxUseSelector,
    TypedUseSelectorHook,
} from "react-redux";

import settingReducer from "./slices/setting";
import messageReducer from "./slices/message";
import speechReducer from "./slices/speech";
import userReducer from "./slices/user";

const store = configureStore({
    reducer: {
        setting: settingReducer,
        message: messageReducer,
        speech: speechReducer,
        user: userReducer,
    },
    middleware: (getDefaultMiddleware) => getDefaultMiddleware({
        serializableCheck: {
            ignoreActions: true
        }
    }).concat([reduxWebsocket()])
});

export type AppDispatch = typeof store.dispatch

export const useDispatch: () => typeof store.dispatch = ReduxUseDispatch;
export const useSelector: TypedUseSelectorHook<ReturnType<typeof store.getState>> = ReduxUseSelector;

export default store;
