import { createSlice } from '@reduxjs/toolkit'
import { ReceivedMessage } from '../protocol';

export type SpeechState = {
  isEnabled: boolean,
  isReady: boolean,
  content: string,
};

const initialState : SpeechState = {
  isEnabled: true,
  isReady: false,
  content: ""
};

function clean_text(text: string){
  return text.split(/```\n?/).filter(
    (elem, idx) => {
      return ((idx % 2) === 0) && (elem.length !== 0)
    }
  ).join(" ");
}

export const speechSlice = createSlice({
  name: 'speech',
  initialState,
  reducers: {
    disableSpeech: (state) => {
      state.isEnabled = false;
    },
    enableSpeech: (state) => {
      state.isEnabled = true;
    },
    resetSpeech: (state) => {
      state.isReady = false;
      state.content = "";
    }
  },
  extraReducers(builder) {
    builder.addCase("REDUX_WEBSOCKET::MESSAGE", (state, action: any) => {
      if(state.isEnabled){
        const receivedMedssage : ReceivedMessage = JSON.parse(action.payload.message);
        if(receivedMedssage.message_type === "text"){
          state.content += clean_text(receivedMedssage.message);
          if(!receivedMedssage.parameters.stream || receivedMedssage.parameters.is_end){
            state.isReady = true;
          }
        }
      }
    });
  },
});

export const { disableSpeech, enableSpeech, resetSpeech } = speechSlice.actions;

export default speechSlice.reducer;
