import React, {useState} from 'react';
import {Spinner} from 'react-bootstrap';
import SplitPane, {Pane} from 'split-pane-react';

import 'split-pane-react/esm/themes/default.css'

import './MainPage.css';
import '../components/Message.css';
import { useSelector } from '../store';
import ChatBox from "../components/ChatBox";
import EditorBox from '../components/EditorBox';
import Socket from '../components/Socket';
import Speech from '../components/Speech';
import Navbar from '../components/Navbar';

function MainPage() {
  const [splitSize, setSplitSize] = useState<Array<string | number>>(['50%', '50%']);

  const colorScheme = useSelector(state => state.setting.colorScheme);
  const messageState = useSelector(state => state.message);

  return (
    <div id="MainPage" className={colorScheme}>
      <Navbar />
      <SplitPane
        split='vertical'
        sizes={splitSize}
        onChange={setSplitSize}
        sashRender={() => null}
      >
        <Pane minSize='200px' className="column">
          <ChatBox />
        </Pane>
        <Pane minSize='200px' className="column">
          <EditorBox />
        </Pane>
      </SplitPane>
      {messageState.isOpened ? "" :
        <div id="loading-screen">
          <h1>Connecting</h1>
          <Spinner animation="border" variant="light"/>
        </div>
      }
      <Socket />
      <Speech />
    </div>
  );
}

export default MainPage;
