import { send } from '@giantmachines/redux-websocket/dist';
import { createSlice } from '@reduxjs/toolkit';
import { ReceivedMessage, sendMessage } from '../protocol';

export type MessageType = {
  role: 'bot' | 'user',
  text: string,
  type: 'text' | 'error',
};

export type MessageState = {
  isOpened: boolean,
  isSending: boolean,
  isStreaming: boolean,
  messages: Array<MessageType>,
};

const initialState : MessageState = {
  isOpened: false,
  isSending: false,
  isStreaming: false,
  messages: [],
};

export const messageSlice = createSlice({
  name: 'message',
  initialState,
  reducers: {
    sendChat: (state, action) => {
      state.messages.push({role: 'user', text: action.payload, type: "text"});
    },
    removeMessage: (state, action) => {
      state.messages.splice(action.payload, 1);
    },
    sending: (state) => {
      state.isSending = true;
    }
  },
  extraReducers(builder) {
    builder.addCase("REDUX_WEBSOCKET::OPEN", (state) => {
      state.isOpened = true;
    });
    builder.addCase("REDUX_WEBSOCKET::MESSAGE", (state, action: any) => {
      const receivedMedssage : ReceivedMessage = JSON.parse(action.payload.message);
      const lastMessage = state.messages[state.messages.length - 1];
      if(state.isStreaming && (lastMessage.type === receivedMedssage.message_type)){
        state.messages[state.messages.length - 1].text += receivedMedssage.message;
      }else{
        state.messages.push({
          role: 'bot',
          text: receivedMedssage.message,
          type: receivedMedssage.message_type
        });
      }
      state.isStreaming = receivedMedssage.parameters.stream && !receivedMedssage.parameters.is_end;
      state.isSending = !receivedMedssage.parameters.is_end;
    });
    builder.addCase("REDUX_WEBSOCKET::CLOSED", (state) => {
      state.isOpened = false;
      state.isSending = false;
      state.isStreaming = false;
      state.messages = [];
    });
  },
});

export const sendChat = (chat: string) => {
  return async function(dispatch: any){
    dispatch(messageSlice.actions.sending());
    dispatch(messageSlice.actions.sendChat(chat));
    dispatch(send(sendMessage(chat, "text")));
  }
}

export const sendCode = (code: string) => {
  return async function(dispatch: any){
    dispatch(messageSlice.actions.sending());
    dispatch(send(sendMessage(code, "code")));
  }
}

export const removeMessage = messageSlice.actions.removeMessage;

export default messageSlice.reducer;