import React, {useEffect} from 'react';
import { useDispatch, useSelector } from '../store';
import { resetSpeech } from '../slices/speech';

const synthesis = window.speechSynthesis;
const voice = synthesis.getVoices().filter(v => v.voiceURI === 'Google US English')[0];

const Speech = () => {

    const dispatch = useDispatch();
    const speechState = useSelector(state => state.speech);

    useEffect(() => {
        if(speechState.isReady){
            const utterance = new SpeechSynthesisUtterance(speechState.content);
            utterance.voice = voice;
            utterance.rate = 0.95;
            synthesis.pause();
            synthesis.cancel();
            synthesis.speak(utterance);
            dispatch(resetSpeech());
        }
    }, [speechState.isReady]);

    useEffect(() => {
        if(!speechState.isEnabled){
            synthesis.pause();
            synthesis.cancel();
        }
    }, [speechState.isEnabled]);

    return <></>;
}

export default Speech;