import React from "react";
import { Button, NavDropdown, Navbar as ReactNavbar } from 'react-bootstrap';
import { CircleHalf } from "react-bootstrap-icons";
import { useDispatch, useSelector } from "../store";
import { setColorScheme } from "../slices/setting";
import { logout } from "../slices/user";

function Navbar(){

    const dispatch = useDispatch();
    const colorScheme = useSelector(state => state.setting.colorScheme);
    const currentUser = useSelector(state => state.user.currentUser);

    const onToggleColorScheme = () => {
        dispatch(setColorScheme((colorScheme === 'dark') ? 'light' : 'dark'));
    }

    const onLogout = () => {
        dispatch(logout());
    }

    return (
        <ReactNavbar id="navbar" bg='secondary' variant='dark'>
            <ReactNavbar.Brand>ChatGPT Interview</ReactNavbar.Brand>
            <ReactNavbar.Toggle/>
            <ReactNavbar.Collapse className="justify-content-end">
                <Button variant='link' className='text-light' onClick={onToggleColorScheme}><CircleHalf/></Button>
                {currentUser ?
                    <NavDropdown title={currentUser} menuVariant={colorScheme} align='end'>
                        <NavDropdown.Item onClick={onLogout}>Log out</NavDropdown.Item>
                    </NavDropdown>
                : null}
            </ReactNavbar.Collapse>
        </ReactNavbar>
    )
}

export default Navbar;
